<template><section><h1>BillboardCollection</h1>
<p>The <code v-pre>vc-collection-primitive-billboard</code> component is used to load a renderable collection of billboards. Billboards are viewport-aligned images positioned in the 3D scene.</p>
<h2>Example</h2>
<h3>Load BillboardCollection with <code v-pre>vc-collection-primitive-billboard</code></h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-collection-primitive-billboard ref="billboardCollection" :billboards="billboards" @click="clicked" @mouseout="mouseout" @mouseover="mouseover"></vc-collection-primitive-billboard>
        <vc-collection-primitive-billboard>
          <vc-primitive-billboard @click="clicked" @mouseout="mouseout" @mouseover="mouseover" :image="image" :scale="scale" :show="show" :distanceDisplayCondition="distanceDisplayCondition" :horizontalOrigin="horizontalOrigin" :position="position"></vc-primitive-billboard>
        </vc-collection-primitive-billboard>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
