<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>modelMatrix</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> The 4x4 transformation matrix that transforms each billboard from model to world coordinates.</td>
</tr>
<tr>
<td>debugShowBoundingVolume</td>
<td>Boolean</td>
<td><code v-pre>false</code></td>
<td><code v-pre>optional</code> For debugging only. Determines if this primitive's commands' bounding spheres are shown.</td>
</tr>
<tr>
<td>blendOption</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> The billboard blending option. The default is used for rendering both opaque and translucent billboards. However, if either all of the billboards are completely opaque or all are completely translucent, setting the technique to BlendOption.OPAQUE or BlendOption.TRANSLUCENT can improve performance by up to 2x.</td>
</tr>
<tr>
<td>scene</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> Must be passed in for billboards that use the height reference property or will be depth tested against the globe.</td>
</tr>
<tr>
<td>billboards</td>
<td>Array</td>
<td><code v-pre>[]</code></td>
<td><code v-pre>optional</code> Specifies an array of label collections. The array object structure is a <a href="./#/zh/primitive/vc-primitive-billboard"><code v-pre>vc-primitive-billboard</code></a> component property.</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>Refer to the official document: <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/BillboardCollection.html">BillboardCollection</a></strong></li>
</ul>
<h2>Events</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>parameter</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>Triggers when the component is ready. It returns a core class of Cesium, a viewer instance, and the cesiumObject.</td>
</tr>
<tr>
<td>mousedown</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>Triggered when the mouse is pressed on the collection of primitives.</td>
</tr>
<tr>
<td>mouseup</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>Triggered when the mouse bounces on the collection of primitives.</td>
</tr>
<tr>
<td>click</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>Triggered when the mouse clicks on the collection of primitives.</td>
</tr>
<tr>
<td>clickout</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>Touch when the mouse clicks outside the collection of primitives.</td>
</tr>
<tr>
<td>dblclick</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>Triggered when the left mouse button double-clicks the collection of primitives.</td>
</tr>
<tr>
<td>mousemove</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>Triggered when the mouse moves on the collection of primitives.</td>
</tr>
<tr>
<td>mouseover</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>Triggered when the mouse moves to the collection of primitives.</td>
</tr>
<tr>
<td>mouseout</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>Triggered when the mouse moves out of the collection of primitives.</td>
</tr>
</tbody>
</table>
